<template>
    <div>
        <h1>{{ user.name }}</h1>
        <input type="text" v-model="user.name" v-on:keyup="typing"/>
        <button v-on:click="saludar(user.name)">Saludar</button>
        <h3>Mensaje: {{msg}}</h3>
    </div>
</template>

<script >
 export default {
     data() {
         return {
             title: "chat",
             body: "Esto es un mensaje",
             user: {
                 name: "Roberto",
                 lastname: "Suárez"
             }
         }
     },
     methods: {
         saludar(name) {
             alert('Hola, ' + name)
         },

         typing(e) {
             if (e.keyCode == 13) {
                 this.saludar(this.user.name)
             }
         }
     },
     props: {
         msg: {
             type: String,
             default: 'no mensaje'
         }
     }

 }
</script>



